<template>
  <v-flex>
    <v-data-table
      :headers="headers"
      :items="ventas"
      :search="search"
      class="elevation-1"
      v-if="verNuevo == 0"
    >
      <template v-slot:item.estado="{ item }">
        <div v-if="item.estado">
          <v-btn text color="green">Aceptado</v-btn>
        </div>
        <div v-else>
          <v-btn text color="red">Anulado</v-btn>
        </div>
      </template>

      <template v-slot:item.usuario="{ item }">
        <div>
          {{ item.usuario.nombre }}
        </div>
      </template>

      <template v-slot:item.persona="{ item }">
        <div>
          {{ item.persona.nombre }}
        </div>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon small class="mr-2" @click="verIngreso(item)">
          tab
        </v-icon>
        <v-icon small class="mr-2" @click="mostrarComprobante(item)">
          print
        </v-icon>
        <template v-if="item.estado">
          <v-icon small @click="activarDesactivarMostrar(2, item)">
            block
          </v-icon>
        </template>
        <template v-else>
          <v-icon small @click="activarDesactivarMostrar(1, item)">
            check
          </v-icon>
        </template>
      </template>

      <template v-slot:top>
        <v-toolbar text color="white">
          <v-toolbar-title>Ventas</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-text-field
            class="text-xs-center"
            v-model="search"
            append-icon="search"
            label="Búsqueda"
            single-line
            hide-details
            v-if="verNuevo == 0"
          ></v-text-field>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            dark
            class="mb-2"
            v-if="verNuevo == 0"
            @click="mostrarNuevo()"
          >
            Nueva Venta
          </v-btn>

          <v-dialog v-model="adModal" max-width="290">
            <v-card>
              <v-card-title class="headline" v-if="adAccion == 1">
                Activar Item
              </v-card-title>
              <v-card-title class="headline" v-if="adAccion == 2">
                Desactivar Item
              </v-card-title>
              <v-card-text>
                Estás a punto de <span v-if="adAccion == 1">activar </span>
                <span v-if="adAccion == 2">desactivar </span> el item
                {{ adNombre }}
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn @click="activarDesactivarCerrar()" color="green">
                  Cancelar
                </v-btn>
                <v-btn v-if="adAccion == 1" @click="activar()" color="orange">
                  Activar
                </v-btn>
                <v-btn
                  v-if="adAccion == 2"
                  @click="desactivar()"
                  color="orange"
                >
                  Desactivar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="comprobanteModal" max-width="1000">
            <v-card>
              <v-card-title class="head-line">
                <v-btn>
                  <v-icon @click="crearPDF()">
                    print
                  </v-icon>
                </v-btn>
                Reporte de venta
              </v-card-title>

              <v-card-text>
                <div id="factura">
                  <header>
                    <div id="logo">
                      <img
                        src="data:image/png;base64,"
                        id="imagen"
                      />
                    </div>
                    <div id="datos">
                      <p id="encabezado">
                        <b>IncanatoIT</b>
                        <br />José Gálvez 1368, Chongoyape - Chiclayo, Perú<br />Telefono:(+51)931742904<br />Email:jcarlos.ad7@gmail.com
                      </p>
                    </div>
                    <div id="fact">
                      <p>
                        {{ tipo_comprobante }}<br />
                        {{ serie_comprobante }}-{{ num_comprobante }}<br />
                        {{ fecha }}
                      </p>
                    </div>
                  </header>
                  <br />
                  <section>
                    <div>
                      <table id="facliente">
                        <tbody>
                          <tr>
                            <td id="cliente">
                              <strong>Sr(a). {{ persona.nombre }}</strong
                              ><br />
                              <strong>Documento:</strong>
                              {{ persona.num_documento }}<br />
                              <strong>Dirección:</strong> {{ persona.direccion
                              }}<br />
                              <strong>Teléfono:</strong> {{ persona.telefono
                              }}<br />
                              <strong>Email:</strong> {{ persona.email }}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </section>
                  <br />
                  <section>
                    <div>
                      <table id="facarticulo">
                        <thead>
                          <tr id="fa">
                            <th>CANT</th>
                            <th>DESCRIPCION</th>
                            <th>PRECIO UNIT</th>
                            <th>DESC.</th>
                            <th>PRECIO TOTAL</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="det in detalles" :key="det._id">
                            <td style="text-align:center;">
                              {{ det.cantidad }}
                            </td>
                            <td>{{ det.articulo }}</td>
                            <td style="text-align:right;">{{ det.precio }}</td>
                            <td style="text-align:right;">
                              {{ det.descuento }}
                            </td>
                            <td style="text-align:right;">
                              {{ det.cantidad * det.precio - det.descuento }}
                            </td>
                          </tr>
                        </tbody>
                        <tfoot>
                          <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th style="text-align:right;">SUBTOTAL</th>
                            <th style="text-align:right;">
                              $
                              {{
                                (totalParcial = (total - totalImpuesto).toFixed(
                                  2
                                ))
                              }}
                            </th>
                          </tr>
                          <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th style="text-align:right;">
                              IVA({{ impuesto }}%)
                            </th>
                            <th style="text-align:right;">
                              $
                              {{
                                (totalImpuesto = (
                                  (total * impuesto) /
                                  (1 + impuesto)
                                ).toFixed(2))
                              }}
                            </th>
                          </tr>
                          <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th style="text-align:right;">TOTAL</th>
                            <th style="text-align:right;">
                              $ {{ (total = calcularTotal) }}
                            </th>
                          </tr>
                        </tfoot>
                      </table>
                    </div>
                  </section>
                  <br />
                  <br />
                  <footer>
                    <div id="gracias">
                      <p><b>Gracias por su compra!</b></p>
                    </div>
                  </footer>
                </div>
              </v-card-text>

              <v-card-actions>
                <v-btn color="blue darken-1" text @click="ocultarComprobante()">
                  Cancelar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
        <v-spacer></v-spacer>
      </template>

      <template v-slot:no-data>
        <v-btn color="primary" @click="listar()">Actualizar</v-btn>
      </template>
    </v-data-table>
    <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
      <v-layout row wrap>
        <v-flex xs12 sm4 md4 lg4 x14>
          <v-select
            v-model="tipo_comprobante"
            :items="comprobantes"
            label="Tipo Comprobante"
          >
          </v-select>
        </v-flex>

        <v-flex xs12 sm4 md4 lg4 xl4>
          <v-text-field v-model="serie_comprobante" label="Serie Comprobante">
          </v-text-field>
        </v-flex>

        <v-flex xs12 sm4 md4 lg4 xl4>
          <v-text-field v-model="num_comprobante" label="Número Comprobante">
          </v-text-field>
        </v-flex>

        <v-flex xs12 sm4 md8 lg8 xl8>
          <v-autocomplete :items="personas" label="Cliente" v-model="persona">
          </v-autocomplete>
        </v-flex>

        <v-flex xs12 sm4 md4 lg4 xl4>
          <v-text-field type="number" v-model="impuesto" label="Impuesto">
          </v-text-field>
        </v-flex>

        <v-flex xs12 sm8 md8 lg8 xl8>
          <v-text-field
            v-model="codigo"
            label="Código"
            @keyup.enter="buscarCodigo()"
          >
          </v-text-field>
        </v-flex>

        <v-flex xs12 sm2 md2 lg2 xl2>
          <v-btn small fab dark color="teal" @click="mostrarModalArticulos()">
            <v-icon dark>list</v-icon>
          </v-btn>
        </v-flex>

        <v-flex xs12 sm2 md2 lg2 xl2 v-show="errorArticulo">
          <div class="red--text" v-text="errorArticulo"></div>
        </v-flex>

        <!-- dialogo para seleccion de articulo -->
        <v-dialog v-model="dialog" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">Seleccione artículos.</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 lg12 xl12>
                    <v-text-field
                      class="text-xs-center"
                      v-model="texto"
                      append-icon="search"
                      label="Búsqueda"
                      @keyup.enter="listarArticulos()"
                    ></v-text-field>
                    <template>
                      <v-data-table
                        :headers="cabeceraArticulos"
                        :items="articulos"
                        hide-default-footer
                        class="elevation-1"
                      >
                        <template v-slot:item.seleccionar="{ item }">
                          <v-icon
                            @click="agregarDetalle(item)"
                            small
                            class="mr-2"
                          >
                            add
                          </v-icon>
                        </template>

                        <template v-slot:item.estado="{ item }">
                          <div v-if="item.estado">
                            <v-btn text color="green">Activo</v-btn>
                          </div>
                          <div v-else>
                            <v-btn text color="red">Inactivo</v-btn>
                          </div>
                        </template>
                      </v-data-table>
                    </template>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-flex xs12 sm12 md12 lg12 xl12>
          <template>
            <v-data-table
              :headers="cabeceraDetalles"
              :items="detalles"
              hide-default-footer
              class="elevation-1"
            >
              <template v-slot:item.borrar="{ item }">
                <v-icon
                  @click="eliminarDetalle(detalles, item)"
                  small
                  class="mr-2"
                >
                  delete
                </v-icon>
              </template>

              <template v-slot:item.cantidad="{ item }">
                <v-text-field v-model="item.cantidad" type="number">
                </v-text-field>
              </template>

              <template v-slot:item.precio="{ item }">
                <v-text-field v-model="item.precio" type="number">
                </v-text-field>
              </template>

              <template v-slot:item.descuento="{ item }">
                <v-text-field v-model="item.descuento" type="number">
                </v-text-field>
              </template>

              <template v-slot:item.subtotal="{ item }">
                <div>$ {{ item.cantidad * item.precio - item.descuento }}</div>
              </template>

              <template v-slot:no-data>
                <h3>
                  No hay Articulos Agregados al detalle.
                </h3>
              </template>
            </v-data-table>

            <v-flex class="text-lg-right">
              <strong>Total Parcial:</strong> $
              {{ (totalParcial = (total - totalImpuesto).toFixed(2)) }}
            </v-flex>
            <v-flex class="text-lg-right">
              <strong>Total Impuesto:</strong> $
              {{
                (totalImpuesto = ((total * impuesto) / (1 + impuesto)).toFixed(
                  2
                ))
              }}
            </v-flex>
            <v-flex class="text-lg-right">
              <strong>Total Neto:</strong> $ {{ (total = calcularTotal) }}
            </v-flex>
          </template>
        </v-flex>

        <v-flex xs12 sm12 md12 v-show="valida">
          <div
            class="red--text"
            v-for="v in validaMensaje"
            :key="v"
            v-text="v"
          ></div>
        </v-flex>

        <v-flex xs12 sm12 md12 xl12>
          <v-btn color="blue darlen-1" text @click.native="ocultarNuevo()">
            Cancelar
          </v-btn>
          <v-btn
            color="success"
            v-if="verDetalle == 0"
            @click.native="guardar()"
          >
            Guardar
          </v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </v-flex>
</template>

<script>
import axios from "axios";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default {
  data() {
    return {
      dialog: false,
      search: "",
      ventas: [],
      headers: [
        { text: "Opciones", value: "actions", sortable: false },
        { text: "Usuario", value: "usuario.nombre", sortable: true },
        { text: "Cliente", value: "persona.nombre", sortable: true },
        { text: "Tipo Comprobante", value: "tipo_comprobante", sortable: true },
        {
          text: "Serie Comprobante",
          value: "serie_comprobante",
          sortable: false
        },
        {
          text: "Número Comprobante",
          value: "num_comprobante",
          sortable: false
        },
        { text: "Fecha", value: "createdAt", sortable: false },
        { text: "Impuesto", value: "impuesto", sortable: false },
        { text: "Total", value: "total", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],

      _id: "",
      persona: "",
      personas: [],
      tipo_comprobante: "",
      comprobantes: ["BOLETA", "FACTURA", "TICKET", "GUIA"],
      serie_comprobante: "",
      num_comprobante: "",
      impuesto: 0.18,

      codigo: "",
      cabeceraDetalles: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Articulo", value: "articulo", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Sub Total", value: "subtotal", sortable: false }
      ],
      detalles: [],
      verNuevo: 0,
      errorArticulo: null,
      total: 0,
      totalParcial: 0,
      totalImpuesto: 0,

      articulos: [],
      texto: "",
      cabeceraArticulos: [
        { text: "Seleccionar", value: "seleccionar", sortable: false },
        { text: "Código", value: "codigo", sortable: false },
        { text: "Nombre", value: "nombre", sortable: true },
        { text: "Categoria", value: "categoria.nombre", sortable: true },
        { text: "Stock", value: "stock", sortable: false },
        { text: "Precio Venta", value: "precio_venta", sortable: false },
        { text: "Descripcion", value: "descripcion", sortable: false },
        { text: "Estado", value: "estado", sortable: false }
      ],
      verDetalle: 0,

      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      comprobanteModal: 0,
      fecha: null
    };
  },
  computed: {
    calcularTotal: function() {
      let resultado = 0.0;
      this.detalles.forEach(detalle => {
        resultado =
          resultado + (detalle.cantidad * detalle.precio - detalle.descuento);
      });
      return resultado;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.listar();
    this.selectPersona();
  },

  methods: {
    crearPDF() {
      console.log("entre aca");
      var quotes = document.getElementById("factura");
      html2canvas(quotes).then(function (canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 210;
        var pageHeight = 295;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteVenta.pdf");
      });
    },
    mostrarComprobante(item) {
      this.limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.serie_comprobante = item.serie_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.fecha = item.createdAt;
      this.persona = item.persona;
      this.impuesto = item.impuesto;
      this.listarDetalles(item._id);
      this.comprobanteModal = 1;
    },
    ocultarComprobante() {
      this.comprobanteModal = 0;
    },
    selectPersona() {
      let me = this;
      let personaArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("persona/listClientes", configuracion)
        .then(function(response) {
          personaArray = response.data;
          personaArray.map(x => {
            me.personas.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    buscarCodigo() {
      let me = this;
      me.errorArticulo = null;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("articulo/queryCodigo?codigo=" + this.codigo, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorArticulo = "No existe el articulo";
          console.log(error);
        });
    },
    agregarDetalle(data) {
      this.errorArticulo = null;
      if (this.encuentra(data._id)) {
        this.errorArticulo = "Ya agregaste el articulo";
      } else {
        this.detalles.push({
          _id: data._id,
          articulo: data.nombre,
          cantidad: 1,
          precio: data.precio_venta,
          descuento: 0
        });
      }
    },
    encuentra(id) {
      let sw = false;
      this.detalles.forEach(articulo => {
        if (articulo._id == id) {
          sw = true;
        }
      });
      return sw;
    },
    eliminarDetalle(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },
    listarArticulos() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("articulo/list?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    mostrarModalArticulos() {
      this.dialog = 1;
    },
    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("venta/query?_id=" + id, configuracion)
        .then(function(response) {
          me.detalles = response.data.detalles;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    verIngreso(item) {
      this.limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.serie_comprobante = item.serie_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.persona = item.persona._id;
      this.impuesto = item.impuesto;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetalle = 1;
    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("venta/list", configuracion)
        .then(function(response) {
          me.ventas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    limpiar() {
      this._id = "";
      this.tipo_comprobante = "";
      this.serie_comprobante = "";
      this.num_comprobante = "";
      this.impuesto = 0.18;
      this.codigo = "";
      this.total = 0;
      this.totalParcial = 0;
      this.totalImpuesto = 0;
      this.detalles = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.persona) {
        this.validaMensaje.push("Seleccione un Cliente.");
      }
      if (!this.tipo_comprobante) {
        this.validaMensaje.push("Seleccione un tipo de comprobante.");
      }
      if (!this.num_comprobante) {
        this.validaMensaje.push("Ingrese el número del comprobante.");
      }
      if (!this.impuesto || this.impuesto < 0 || this.impuesto > 1) {
        this.validaMensaje.push("Ingrese un impuesto válido.");
      }
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un artículo al detalle");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    mostrarNuevo() {
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      if (this.validar()) {
        return;
      }
      axios
        .post(
          "venta/add",
          {
            persona: this.persona,
            usuario: this.$store.state.usuario._id,
            tipo_comprobante: this.tipo_comprobante,
            serie_comprobante: this.serie_comprobante,
            num_comprobante: this.num_comprobante,
            impuesto: this.impuesto,
            total: this.total,
            detalles: this.detalles
          },
          configuracion
        )
        .then(function(response) {
          me.limpiar();
          me.close();
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.num_comprobante;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    close() {
      this.dialog = false;
    },

    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("venta/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("venta/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>
